@import 'header';
@import 'recap.app';
@import 'medias.app';
@import 'popup.ui';
@import url('https://fonts.googleapis.com/css2?family=Bricolage+Grotesque:opsz,wght@10..48,300;10..48,400;10..48,500&family=Inter:wght@300;400;500&display=swap');

body,
html {
  display: flex;
  flex-direction: column;

  margin: 0;
  padding: 0;

  width: 100%;
  height: 100%;
}

body:has(div.popUp) {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
b {
  font-family: "Bricolage Grotesque", sans-serif;
}

main {
  width: 95%;
  margin: 0 auto;
}