@use "sass:color";

#recap {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    .steps {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .multi {
            display: flex;
            gap: 2rem;
            width: 100%;

            .entry {
                max-width: 50%;
                width: 50%;
            }
        }

        .entry {
            display: flex;
            gap: 1rem;
            width: 100%;
            height: 100%;

            .timeline {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: stretch;

                height: 100%;
                width: fit-content;
                min-height: 4rem;

                $color: rgb(74, 74, 166);

                .line {
                    width: .15rem;
                    height: 48px;
                    background: color.adjust($color, $lightness: -15%);
                }

                .dot {
                    border-radius: 2rem;
                    width: 1rem;
                    height: 1rem;
                    background: $color;
                    border: solid .15rem white;
                    box-shadow: 0 0 0 .175rem color.adjust($color, $lightness: -10%);
                }
            }

            .content {
                display: flex;
                flex-direction: column;
                width: 100%;

                .text {
                    display: flex;
                    flex-direction: column;

                    margin: auto 0;

                    h3,
                    p {
                        margin: 0;
                    }

                    .time {
                        text-transform: uppercase;
                        font-size: .8rem;
                        font-weight: 300;
                    }

                    .title {
                        margin-bottom: .1rem;
                        font-size: 1.3rem;
                    }
                }

                .media {
                    display: flex;
                    flex-direction: column;
                    gap: .35rem;
                    margin-top: .75rem;

                    h4 {
                        margin: 0;
                    }

                }
            }
        }
    }

    .final {
        width: 90%;
        margin: 1rem auto;

        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;

        text-align: center;

        h2 {
            margin: 0;
        }

        p {
            margin: .5rem 0;
        }

        p:nth-child(3) {
            margin: 0 0 2rem 0;
        }

        .post-card {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: .5rem;

            .media-element {
                width: 100%;
                height: 10rem;
                background-size: cover;
                border-radius: .5rem;
                box-shadow: 0 .25rem .5rem 0 rgba(0, 0, 0, .15);
                transition: 200ms ease-in-out;

                &:hover {
                    cursor: pointer;
                    transform: rotateX(-25deg);
                    box-shadow: 0 1rem .65rem 0 rgba(0, 0, 0, .1)
                }
            }
        }
    }

}